/**
 * RealtySoft Widget v3 - Built Area Component
 * Variations: 1=Min/Max inputs, 2=Range slider
 */

import { RSBaseComponent } from '../base';
import type { ComponentOptions, ComponentConstructor, RealtySoftModule } from '../../types/index';

// Declare global RealtySoft
declare const RealtySoft: RealtySoftModule;

class RSBuiltArea extends RSBaseComponent {
  private lockedMode: boolean = false;
  private minValue: number | null = null;
  private maxValue: number | null = null;

  constructor(element: HTMLElement, options: ComponentOptions = {}) {
    super(element, options);
    this.init();
  }

  init(): void {
    this.lockedMode = this.isLocked('builtMin') || this.isLocked('builtMax');
    this.minValue = this.getFilter<number | null>('builtMin');
    this.maxValue = this.getFilter<number | null>('builtMax');

    this.render();

    // Apply locked styles if locked (but still show the component)
    if (this.lockedMode) {
      this.applyLockedStyle();
    } else {
      this.bindEvents();
    }

    this.subscribe<number | null>('filters.builtMin', (value) => {
      this.minValue = value;
      this.updateDisplay();
    });

    this.subscribe<number | null>('filters.builtMax', (value) => {
      this.maxValue = value;
      this.updateDisplay();
    });
  }

  render(): void {
    this.element.classList.add('rs-built-area', `rs-built-area--v${this.variation}`);

    switch (this.variation) {
      case '2':
        this.renderSlider();
        break;
      default:
        this.renderInputs();
    }
  }

  private renderInputs(): void {
    this.element.innerHTML = `
      <div class="rs-built-area__wrapper">
        <label class="rs-built-area__label">${this.label('search_built_area')}</label>
        <div class="rs-built-area__inputs">
          <div class="rs-built-area__input-group">
            <input type="number"
                   class="rs-built-area__input rs-built-area__input--min"
                   placeholder="${this.label('search_min')}"
                   value="${this.minValue || ''}"
                   min="0"
                   step="10">
            <span class="rs-built-area__unit">mÂ²</span>
          </div>
          <span class="rs-built-area__separator">-</span>
          <div class="rs-built-area__input-group">
            <input type="number"
                   class="rs-built-area__input rs-built-area__input--max"
                   placeholder="${this.label('search_max')}"
                   value="${this.maxValue || ''}"
                   min="0"
                   step="10">
            <span class="rs-built-area__unit">mÂ²</span>
          </div>
        </div>
      </div>
    `;
  }

  private renderSlider(): void {
    const minVal = this.minValue || 0;
    const maxVal = this.maxValue || 1000;

    this.element.innerHTML = `
      <div class="rs-built-area__wrapper">
        <label class="rs-built-area__label">${this.label('search_built_area')}</label>
        <div class="rs-built-area__slider-wrapper">
          <div class="rs-built-area__slider-values">
            <span class="rs-built-area__slider-min">${minVal} mÂ²</span>
            <span class="rs-built-area__slider-max">${maxVal >= 1000 ? '1000+ mÂ²' : maxVal + ' mÂ²'}</span>
          </div>
          <div class="rs-built-area__slider-track">
            <input type="range" class="rs-built-area__slider rs-built-area__slider--min"
                   min="0" max="1000" step="10" value="${minVal}">
            <input type="range" class="rs-built-area__slider rs-built-area__slider--max"
                   min="0" max="1000" step="10" value="${maxVal}">
          </div>
        </div>
      </div>
    `;
  }

  bindEvents(): void {
    // Input fields
    const minInput = this.element.querySelector<HTMLInputElement>('.rs-built-area__input--min');
    const maxInput = this.element.querySelector<HTMLInputElement>('.rs-built-area__input--max');

    if (minInput) {
      minInput.addEventListener('change', (e: Event) => {
        const target = e.target as HTMLInputElement;
        const value = target.value ? parseInt(target.value) : null;
        this.setFilter('builtMin', value);
      });
    }

    if (maxInput) {
      maxInput.addEventListener('change', (e: Event) => {
        const target = e.target as HTMLInputElement;
        const value = target.value ? parseInt(target.value) : null;
        this.setFilter('builtMax', value);
      });
    }

    // Sliders
    const minSlider = this.element.querySelector<HTMLInputElement>('.rs-built-area__slider--min');
    const maxSlider = this.element.querySelector<HTMLInputElement>('.rs-built-area__slider--max');

    if (minSlider) {
      minSlider.addEventListener('input', (e: Event) => {
        const target = e.target as HTMLInputElement;
        let value = parseInt(target.value);
        if (maxSlider && value > parseInt(maxSlider.value)) {
          value = parseInt(maxSlider.value);
          target.value = String(value);
        }
        this.setFilter('builtMin', value || null);
        this.updateSliderDisplay();
      });
    }

    if (maxSlider) {
      maxSlider.addEventListener('input', (e: Event) => {
        const target = e.target as HTMLInputElement;
        let value = parseInt(target.value);
        if (minSlider && value < parseInt(minSlider.value)) {
          value = parseInt(minSlider.value);
          target.value = String(value);
        }
        this.setFilter('builtMax', value >= 1000 ? null : value);
        this.updateSliderDisplay();
      });
    }
  }

  private updateDisplay(): void {
    // Update inputs
    const minInput = this.element.querySelector<HTMLInputElement>('.rs-built-area__input--min');
    const maxInput = this.element.querySelector<HTMLInputElement>('.rs-built-area__input--max');

    if (minInput) minInput.value = this.minValue?.toString() || '';
    if (maxInput) maxInput.value = this.maxValue?.toString() || '';

    // Update sliders
    this.updateSliderDisplay();
  }

  private updateSliderDisplay(): void {
    const minLabel = this.element.querySelector('.rs-built-area__slider-min');
    const maxLabel = this.element.querySelector('.rs-built-area__slider-max');
    const minSlider = this.element.querySelector<HTMLInputElement>('.rs-built-area__slider--min');
    const maxSlider = this.element.querySelector<HTMLInputElement>('.rs-built-area__slider--max');

    if (minLabel && minSlider) {
      minLabel.textContent = `${minSlider.value} mÂ²`;
    }

    if (maxLabel && maxSlider) {
      const val = parseInt(maxSlider.value);
      maxLabel.textContent = val >= 1000 ? '1000+ mÂ²' : `${val} mÂ²`;
    }
  }
}

// Register component
RealtySoft.registerComponent('rs_built_area', RSBuiltArea as unknown as ComponentConstructor);

export { RSBuiltArea };
export default RSBuiltArea;
