/**
 * RealtySoft Widget v3 - Detail Share Component
 * Social sharing buttons
 */

import { RSBaseComponent } from '../base';
import type { ComponentOptions, Property, RealtySoftLabelsModule, RealtySoftAnalyticsModule } from '../../types/index';

// Declare globals
declare const RealtySoftLabels: RealtySoftLabelsModule;
declare const RealtySoftAnalytics: RealtySoftAnalyticsModule;
declare const RealtySoftToast: { success: (msg: string) => void; error: (msg: string) => void } | undefined;

class RSDetailShare extends RSBaseComponent {
  private property: Property | null = null;
  private hasInitiallyRendered: boolean = false;

  constructor(element: HTMLElement, options: ComponentOptions = {}) {
    super(element, options);
    this.init();
  }

  init(): void {
    // Get property from options (set before super() calls init())
    this.property = this.options?.property as Property | null;

    if (!this.property) {
      this.element.style.display = 'none';
      return;
    }

    this.render();
    this.bindEvents();

    // Listen for language changes to update labels
    this.subscribe('language', () => {
      this.updateLabelsInPlace();
    });
  }

  render(): void {
    // If already rendered, just update labels (language change scenario)
    if (this.hasInitiallyRendered) {
      this.updateLabelsInPlace();
      return;
    }

    // First-time render: build full HTML structure
    this.hasInitiallyRendered = true;

    this.element.classList.add('rs-detail-share');

    this.element.innerHTML = `
      <span class="rs-detail-share__label">${this.label('detail_share') || 'Share'}</span>
      <div class="rs-detail-share__buttons">
        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--whatsapp"
                data-platform="whatsapp"
                title="WhatsApp">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
            <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413z"/>
          </svg>
        </button>

        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--facebook"
                data-platform="facebook"
                title="Facebook">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
            <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
          </svg>
        </button>

        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--twitter"
                data-platform="twitter"
                title="Twitter/X">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
            <path d="M18.244 2.25h3.308l-7.227 8.26 8.502 11.24H16.17l-5.214-6.817L4.99 21.75H1.68l7.73-8.835L1.254 2.25H8.08l4.713 6.231zm-1.161 17.52h1.833L7.084 4.126H5.117z"/>
          </svg>
        </button>

        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--linkedin"
                data-platform="linkedin"
                title="LinkedIn">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
            <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
          </svg>
        </button>

        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--email"
                data-platform="email"
                title="Email">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path>
            <polyline points="22,6 12,13 2,6"></polyline>
          </svg>
        </button>

        <button type="button"
                class="rs-detail-share__btn rs-detail-share__btn--copy"
                data-platform="copy"
                title="Copy Link">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <rect x="9" y="9" width="13" height="13" rx="2" ry="2"></rect>
            <path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"></path>
          </svg>
        </button>
      </div>
    `;
  }

  bindEvents(): void {
    this.element.querySelectorAll<HTMLButtonElement>('.rs-detail-share__btn').forEach(btn => {
      btn.addEventListener('click', () => {
        const platform = btn.dataset.platform || '';

        if (platform === 'copy') {
          this.copyToClipboard();
        } else {
          this.share(platform);
        }

        RealtySoftAnalytics.trackShare(platform, this.property!.id);
      });
    });
  }

  /**
   * Get the URL to share.
   * WordPress: shares window.location.href (OG tags served from transient cache).
   * Non-WordPress: shares a share.php URL on realtysoft.ai with property data as params.
   */
  private getShareUrl(): string {
    const config = (window as any).RealtySoftConfig || {};
    if (config.wpRestUrl) {
      return window.location.href; // WordPress: OG tags from transient cache
    }
    return this.buildSharePhpUrl(); // Non-WordPress: share.php fallback
  }

  /**
   * Build a share.php URL on realtysoft.ai with property data as query params.
   * Used as a fallback for non-WordPress platforms (Wix, Squarespace, etc.).
   */
  private buildSharePhpUrl(): string {
    const p = this.property!;
    const params = new URLSearchParams();
    params.set('ref', p.ref || p.unique_ref || '');
    params.set('title', p.title || '');
    params.set('image', (p.imagesFull?.[0]) || (p.images?.[0]) || '');
    params.set('description', (p.short_description || p.description || '').substring(0, 300));
    params.set('price', RealtySoftLabels.formatPrice(p.price));
    params.set('location', p.location || '');
    params.set('url', window.location.href);
    return `https://realtysoft.ai/propertymanager/share.php?${params.toString()}`;
  }

  /**
   * Share to specific platform.
   * Uses getShareUrl() to determine the correct URL based on platform.
   */
  private share(platform: string): void {
    const p = this.property!;
    const pageUrl = this.getShareUrl();
    const text = encodeURIComponent(`${p.title} - ${RealtySoftLabels.formatPrice(p.price)}`);

    let shareLink = '';

    switch (platform) {
      case 'whatsapp':
        shareLink = `https://wa.me/?text=${encodeURIComponent(p.title + ' - ' + pageUrl)}`;
        break;

      case 'facebook':
        shareLink = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(pageUrl)}`;
        break;

      case 'twitter':
        shareLink = `https://twitter.com/intent/tweet?text=${text}&url=${encodeURIComponent(pageUrl)}`;
        break;

      case 'linkedin':
        shareLink = `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(pageUrl)}`;
        break;

      case 'email': {
        const subject = encodeURIComponent(p.title || 'Check out this property');
        const body = encodeURIComponent(`${p.title}\n${RealtySoftLabels.formatPrice(p.price)}\n${p.location || ''}\n\nView property: ${pageUrl}`);
        shareLink = `mailto:?subject=${subject}&body=${body}`;
        window.location.href = shareLink;
        return;
      }

      default:
        shareLink = pageUrl;
    }

    // Open share dialog
    window.open(shareLink, '_blank', 'width=600,height=500,scrollbars=yes');
  }

  private async copyToClipboard(): Promise<void> {
    const shareUrl = this.getShareUrl();
    try {
      await navigator.clipboard.writeText(shareUrl);
      this.showCopiedFeedback();
    } catch {
      // Fallback for older browsers
      const textarea = document.createElement('textarea');
      textarea.value = shareUrl;
      document.body.appendChild(textarea);
      textarea.select();
      document.execCommand('copy');
      document.body.removeChild(textarea);
      this.showCopiedFeedback();
    }
  }

  private showCopiedFeedback(): void {
    const copyBtn = this.element.querySelector('.rs-detail-share__btn--copy') as HTMLButtonElement | null;
    if (!copyBtn) return;

    const originalTitle = copyBtn.title;
    copyBtn.title = 'Copied!';
    copyBtn.classList.add('rs-detail-share__btn--copied');

    // Show toast notification
    if (typeof RealtySoftToast !== 'undefined' && RealtySoftToast) {
      RealtySoftToast.success('Link copied to clipboard!');
    }

    setTimeout(() => {
      copyBtn.title = originalTitle;
      copyBtn.classList.remove('rs-detail-share__btn--copied');
    }, 2000);
  }

  /**
   * Update only label text nodes on language change
   */
  private updateLabelsInPlace(): void {
    const label = this.element.querySelector('.rs-detail-share__label');
    if (label) label.textContent = this.label('detail_share') || 'Share';
  }
}

// Note: This component is NOT auto-registered.
// It is instantiated by RSDetail.populateComponents() when property data is available.

export { RSDetailShare };
export default RSDetailShare;
